* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: ralewayThin;
  src: url("Raleway-Thin.f786e4e5.ttf");
}

@font-face {
  font-family: ralewayBold;
  src: url("Raleway-VariableFont_wght.395f909d.ttf");
}

body {
  font-family: ralewayThin;
}

.body {
  padding: 0 5px 5px;
}

.header {
  background-color: #f6c794;
  border: 1px solid #ddd;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  width: 80px;
}

.nav-items {
  padding: 0 20px;
}

.nav-items > ul {
  align-items: center;
  gap: 2rem;
  font-size: 1rem;
  font-weight: bold;
  list-style-type: none;
  display: flex;
}

.nav-items > ul > li > a {
  color: #6a80b9;
  padding: 1rem;
  font-family: ralewayBold;
  text-decoration: none;
}

.nav-items > ul > li:first-child > a {
  color: #fff;
  background-color: #6a80b9;
  border-radius: 5px;
}

.nav-items > ul > button {
  color: #fff;
  cursor: pointer;
  background-color: #6a80b9;
  border: 1px solid #c2c1c1;
  border-radius: 3px;
  width: 4rem;
  height: 3rem;
}

.filter-btn {
  color: #155e95;
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #6a80b9;
  border-radius: 3px;
  height: 2rem;
  padding: 1px 4px;
  font-weight: bold;
}

.search-box {
  border: 1px solid #c2c1c1;
  border-radius: 3px;
  height: 100%;
}

.search button {
  color: #fff;
  cursor: pointer;
  background-color: #6a80b9;
  border: 1px solid #c2c1c1;
  height: 2rem;
  padding: 1px 3px;
}

.res-card {
  background-color: #f6c794;
  border-radius: 5px;
  padding: 8px;
  box-shadow: 0 4px 12px #0000001a;
}

.res-logo {
  border-radius: 5px;
  width: 100%;
  height: 30vh;
}

.res-container {
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  gap: 5px;
  display: grid;
}

.res_name {
  color: #155e95;
  letter-spacing: 1px;
  margin-top: .8rem;
  font-size: 1.4rem;
  font-weight: bold;
}

.res_cuisine {
  color: #505050;
  letter-spacing: 1px;
  text-shadow: 0 0 #000;
  font-weight: bold;
}

.res_rating {
  color: #fff6b3;
  letter-spacing: 1px;
  text-shadow: 0 0 #000;
  background-color: #155e95;
  border-radius: 100%;
  padding: 5px;
  font-size: 12px;
  font-weight: bold;
}

.res_cost {
  color: #505050;
  letter-spacing: 1px;
  text-shadow: 0 0 #000;
  font-weight: bold;
}

.shimmer {
  gap: 10px;
  padding: 10px;
  display: flex;
}

.shimmer_card {
  background-color: #ddd;
  width: 200px;
  height: 200px;
  animation: 3s linear infinite shimmer_anim;
}

@keyframes shimmer_anim {
  0% {
    background-color: #eee;
  }

  50% {
    background-color: #bbb;
  }

  100% {
    background-color: #eee;
  }
}

.filter {
  justify-content: space-between;
  gap: 20px;
  margin: .6rem 0;
  display: flex;
}

.rating_cost_wrap {
  border-top: 1px dashed #000;
  border-bottom: 1px dashed #000;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0;
  padding: 4px;
  display: flex;
}

.search {
  gap: 2px;
  display: flex;
}

.all_filter_btn {
  gap: 10px;
  display: flex;
}

.footer {
  text-align: center;
  background-color: #f6c794;
  margin-top: auto;
  padding: 10px;
}

.app {
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

a {
  text-decoration: none;
}
